<script>
import Header from "@/components/Header/BaseHeader";
import Footer from "@/components/Footer";
import Confirm from "@/components/Button/Confirm";
export default {
  name: "Infrastructure",
  components: {
    Header,
    Footer,
    "confirm-button": Confirm
  },
  data() {
    return {
      selected: null,
      selected2: null,
      isMounted: false
    };
  },
  methods: {
    onSelect2() {
      console.log("onselect2");
      this.selected2 = true;
    },
    onSelect() {
      console.log("onselect");
      this.selected = true;
    },
    onDeselect() {
      console.log("ondeselect");
      this.selected = false;
    },
    onDeselect2() {
      console.log("ondeselect2");
      this.selected2 = false;
    }
  },
  computed: {
    contentPadding() {
      if (!this.isMounted) {
        return;
      } else {
        return 2 * this.$refs.base_header.clientHeight;
      }
    },
    logoPadding() {
      if (window.innerWidth < 640) {
        return 40;
      } else if (window.innerWidth < 768 && window.innerWidht > 640) {
        return 50;
      } else if (window.innerWidth > 768) {
        return 60;
      } else {
        return 60;
      }
    }
  },
  mounted() {
    this.isMounted = true;
  }
};
</script>

